import React, { Fragment, useState } from "react";
import {
  List,
  Avatar,
  ListItem,
  IconButton,
  ListItemIcon,
  ListItemText,
  ListItemAvatar,
  Collapse,
  Theme,
  makeStyles,
  createStyles
} from "@material-ui/core";
import {
  ExpandLess as ExpandLessIcon,
  ExpandMore as ExpandMoreIcon
} from "@material-ui/icons";

type Props = {
  button?: any; // <-- TS is really good, expects "true" as a type 👍
  title: string;
  open?: boolean;
  avatar?: boolean;
  onClick?: Function;
  description?: string;
  suffix?: React.ReactNode;
  prefix?: React.ReactNode;
  children?: React.ReactChild;
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    menuItem: {
      display: "block"
    },
    collapse: {
      "& $menuItem": {
        paddingLeft: theme.spacing(4)
      }
    }
  })
);

const ListViewItem: React.FC<Props> = ({
  open,
  title,
  description,
  prefix,
  suffix,
  button,
  avatar,
  onClick,
  children
}) => {
  const classes = useStyles({ hasChildren: !!children });
  const [collapse, setCollapse] = useState(open);

  const handleClick = () => {
    if (children) return setCollapse(!collapse);
    return onClick && onClick();
  };

  return (
    <li className={classes.menuItem}>
      <ListItem
        component="div"
        onClick={handleClick}
        button={button || !!children}
      >
        {prefix && (
          <Fragment>
            {avatar ? (
              <ListItemAvatar>
                <Avatar>{prefix}</Avatar>
              </ListItemAvatar>
            ) : (
              <ListItemIcon>{prefix}</ListItemIcon>
            )}
          </Fragment>
        )}
        <ListItemText primary={title} secondary={description} />
        {children && (
          <Fragment>
            {collapse ? (
              <ExpandLessIcon data-testid="expand-less" />
            ) : (
              <ExpandMoreIcon data-testid="expand-more" />
            )}
          </Fragment>
        )}
        {suffix && !children && <IconButton edge="end">{suffix}</IconButton>}
      </ListItem>
      {children && (
        <Collapse className={classes.collapse} in={collapse} unmountOnExit>
          <List component="div">{children}</List>
        </Collapse>
      )}
    </li>
  );
};

export default ListViewItem;
