import React from "react";
import {
  Checkbox as MuiCheckbox,
  CheckboxProps,
  FormGroup,
  FormControlLabel
} from "@material-ui/core";

type Props = {
  label?: String;
  value?: boolean;
  disabled?: boolean;
  indeterminate?: boolean;
  icon?: React.ElementType;
  size?: CheckboxProps["size"];
  color?: CheckboxProps["color"];
  checkedIcon?: React.ElementType;
  onChange?: CheckboxProps["onChange"];
};

const Checkbox: React.FC<Props> = ({
  size,
  value,
  color,
  label,
  onChange,
  disabled,
  icon: Icon,
  indeterminate,
  checkedIcon: CheckedIcon
}) => {
  const commonProps = {
    size,
    color,
    disabled,
    indeterminate,
    checked: value,
    onChange: onChange || undefined,
    icon: Icon ? <Icon /> : undefined,
    checkedIcon: CheckedIcon ? <CheckedIcon /> : undefined
  };

  return label ? (
    <FormGroup style={{ display: "inline-block" }} row>
      <FormControlLabel
        label={label}
        control={<MuiCheckbox {...commonProps} />}
      />
    </FormGroup>
  ) : (
    <MuiCheckbox {...commonProps} />
  );
};

export default Checkbox;
