import React from "react";
import ListViewItem from "./ListViewItem";
import ListViewGroup from "./ListViewGroup";
import { List, ListSubheader } from "@material-ui/core";

type Props = {
  children: any;
  title?: string;
  maxHeight?: number;
};

const ListView: React.FC<Props> = ({ title, maxHeight, children }) => {
  const getSubheader = () => {
    if (title) return <ListSubheader>{title}</ListSubheader>;
    if (maxHeight) return <li />;
    return undefined;
  };

  return (
    <List
      style={{
        maxHeight,
        backgroundColor: "inherit",
        overflow: maxHeight ? "auto" : "visible"
      }}
      subheader={getSubheader()}
    >
      {children({ Item: ListViewItem, Group: ListViewGroup })}
    </List>
  );
};

export default ListView;
