import React, { useState } from "react";
import {
  Step,
  StepLabel,
  Typography,
  StepButton,
  Stepper as MuiStepper
} from "@material-ui/core";

type StepsType = {
  label?: String;
  selected?: boolean;
  disabled?: boolean;
  description?: String;
  icon?: React.ElementType;
  content?: React.ElementType;
};

type Props = {
  children?: any;
  steps: StepsType[];
  buttonSteps?: boolean;
  alternative?: boolean;
};

const Stepper: React.FC<Props> = ({
  steps,
  children,
  alternative,
  buttonSteps
}) => {
  const defaultStep = steps.findIndex((step) => !!step.selected);
  const [activeStep, setActiveStep] = useState(
    defaultStep >= 0 ? defaultStep : 0
  );
  const ActiveStep = steps[activeStep]?.content;

  const buttonStepsProps = ({ icon: Icon, ...step }: StepsType) => {
    return {
      ...(Icon && { icon: <Icon /> }),
      optional: step.description ? (
        <Typography variant="caption">{step.description}</Typography>
      ) : undefined
    };
  };

  return (
    <div>
      <MuiStepper
        nonLinear={buttonSteps}
        activeStep={activeStep}
        alternativeLabel={alternative}
        style={{ backgroundColor: "transparent" }}
      >
        {steps.map((step, index) => (
          <Step key={index}>
            {buttonSteps ? (
              <StepButton
                {...buttonStepsProps(step)}
                disabled={step.disabled}
                onClick={() => setActiveStep(index)}
              >
                {step.label}
              </StepButton>
            ) : (
              <StepLabel
                StepIconComponent={step.icon}
                optional={
                  step.description ? (
                    <Typography variant="caption">
                      {step.description}
                    </Typography>
                  ) : undefined
                }
              >
                {step.label}
              </StepLabel>
            )}
          </Step>
        ))}
      </MuiStepper>
      {ActiveStep && <ActiveStep />}
      {children &&
        children({ activeStep, setActiveStep, currentStep: steps[activeStep] })}
    </div>
  );
};

export default Stepper;
