import React from "react";
import {
  Paper,
  Table as MuiTable,
  TableProps,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
  TableSortLabel,
  Toolbar,
  Typography
} from "@material-ui/core";

type Props = TableProps & {
  actions?: React.ReactNode;
  title?: React.ReactNode | string;
};

type TableType = React.FC<Props> & {
  Body?: any;
  Cell?: any;
  Container?: any;
  Head?: any;
  Pagination?: any;
  Row?: any;
  SortLabel?: any;
};

const Table: TableType = ({ actions, title, ...props }) => {
  const hasWrapper = title || actions;

  return hasWrapper ? (
    <Paper>
      <Toolbar>
        <Typography variant="h6">{title}</Typography>
        {actions}
      </Toolbar>
      <TableContainer>
        <MuiTable {...props} />
      </TableContainer>
    </Paper>
  ) : (
    <TableContainer>
      <MuiTable {...props} />
    </TableContainer>
  );
};

Table.Body = TableBody;
Table.Cell = TableCell;
Table.Container = TableContainer;
Table.Head = TableHead;
Table.Pagination = TablePagination;
Table.Row = TableRow;
Table.SortLabel = TableSortLabel;

export default Table;
