import React from "react";
import {
  TreeView as MuiTreeView,
  TreeViewProps,
  TreeItem
} from "@material-ui/lab";
import {
  ExpandMore as ExpandMoreIcon,
  ChevronRight as ChevronRightIcon
} from "@material-ui/icons";

type Props = TreeViewProps & {
  items?: any[];
  multiple?: true;
  disabled?: boolean;
  onIconClick?: React.MouseEventHandler;
  onLabelClick?: React.MouseEventHandler;
  endIcon?: React.ElementType;
  expandIcon?: React.ElementType;
  collapseIcon?: React.ElementType;
};

const TreeView: React.FC<Props> = ({
  items,
  disabled,
  expanded,
  multiple,
  selected,
  onIconClick,
  onLabelClick,
  endIcon: EndIcon,
  expandIcon: ExpandIcon,
  collapseIcon: CollapseIcon
}) => {
  const renderItem = (item: any, index: number) => (
    <TreeItem
      key={index}
      nodeId={item.id}
      label={item.label}
      onIconClick={onIconClick}
      onLabelClick={onLabelClick}
    >
      {item.items && item.items.map(renderItem)}
    </TreeItem>
  );

  return (
    <MuiTreeView
      expanded={expanded}
      multiSelect={multiple}
      selected={selected as any}
      disableSelection={disabled}
      defaultEndIcon={EndIcon ? <EndIcon /> : undefined}
      defaultCollapseIcon={CollapseIcon ? <CollapseIcon /> : <ExpandMoreIcon />}
      defaultExpandIcon={ExpandIcon ? <ExpandIcon /> : <ChevronRightIcon />}
    >
      {items?.map(renderItem)}
    </MuiTreeView>
  );
};

export default TreeView;
