import React, { Fragment } from "react";
import { Button, ButtonProps, makeStyles } from "@material-ui/core";
import { Backup as BackupIcon } from "@material-ui/icons";

const useStyles = makeStyles({
  input: {
    display: "none"
  }
});

type Props = {
  name: string;
  onChange: any;
  label?: string;
  accept?: string;
  multiple?: boolean;
  icon?: React.ElementType;
  color?: ButtonProps["color"];
};

const UploadButton: React.FC<Props> = ({
  name,
  label,
  accept,
  onChange,
  multiple,
  icon: Icon,
  ...props
}) => {
  const classes = useStyles();

  return (
    <Fragment>
      <input
        id={name}
        type="file"
        name={name}
        accept={accept}
        multiple={multiple}
        onChange={onChange}
        className={classes.input}
      />
      <Button
        {...props}
        htmlFor={name}
        component="label"
        variant="contained"
        startIcon={Icon ? <Icon /> : <BackupIcon />}
      >
        {label || "Fazer upload"}
      </Button>
    </Fragment>
  );
};

export default UploadButton;
