import React from "react";
import cn from "classnames";
import { docsEnv } from "../../utils/environment";
import {
  Box,
  LinearProgress,
  LinearProgressProps,
  makeStyles,
  Typography
} from "@material-ui/core";

const useStyles = makeStyles({
  fixedProgress: {
    top: 0,
    left: 0,
    zIndex: 1000,
    width: "100%",
    position: docsEnv() ? "absolute" : "fixed"
  }
});

type Props = LinearProgressProps & {
  fixed: boolean;
  showValue: boolean;
};

const ProgressBar: React.FC<Props> = ({
  value,
  showValue,
  fixed,
  className,
  ...props
}) => {
  const classes = useStyles();

  return !showValue ? (
    <LinearProgress
      {...props}
      value={value}
      className={cn(fixed && classes.fixedProgress, className)}
    />
  ) : (
    <Box display="flex" alignItems="center">
      <Box width="100%" mr={1}>
        <LinearProgress variant="determinate" value={value} {...props} />
      </Box>
      <Box minWidth={35}>
        <Typography variant="body2" color="textSecondary">
          {Math.round(value || 0)}%
        </Typography>
      </Box>
    </Box>
  );
};

export default ProgressBar;
