import React from "react";
import {
  Select,
  MenuItem,
  MenuItemProps,
  InputLabel,
  SelectProps,
  FormControl,
  FormControlProps,
  FormHelperText,
  Theme,
  makeStyles,
  createStyles
} from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    formControl: {
      width: "100%"
    },
    emptyOption: {
      color: theme.palette.grey[500]
    }
  })
);

type OptionType = {
  label?: string | number | any;
  value: MenuItemProps["value"] | any;
};

type Props = {
  name: string;
  onBlur?: any;
  error?: boolean;
  multiple?: boolean;
  disabled?: boolean;
  required?: boolean;
  emptyText?: string;
  autoFocus?: boolean;
  hideEmpty?: boolean;
  placeholder?: string;
  options: OptionType[];
  helperText?: React.ReactNode;
  label?: SelectProps["label"];
  value?: SelectProps["value"];
  size?: FormControlProps["size"];
  onChange?: SelectProps["onChange"];
  variant?: FormControlProps["variant"];
};

const SelectField: React.FC<Props> = ({
  name,
  size,
  error,
  label,
  value,
  variant,
  options,
  disabled,
  multiple,
  required,
  onChange,
  autoFocus,
  hideEmpty,
  emptyText,
  helperText,
  placeholder,
  onBlur,
  ...props
}) => {
  const classes = useStyles();
  const labelId = `${name}-label`;

  return (
    <FormControl
      size={size}
      error={error}
      variant={variant}
      disabled={disabled}
      required={required}
      placeholder={placeholder}
      className={classes.formControl}
    >
      <InputLabel id={labelId}>{label}</InputLabel>
      <Select
        {...props}
        id={name}
        name={name}
        label={label}
        labelId={labelId}
        multiple={multiple}
        autoFocus={autoFocus}
        onBlur={onBlur && onBlur}
        onChange={onChange && onChange}
        value={value == null ? "" : value}
      >
        {!hideEmpty && (
          <MenuItem className={classes.emptyOption} value="">
            {emptyText || "Selecione"}
          </MenuItem>
        )}
        {options.map((option, index) => (
          <MenuItem key={index} value={option.value}>
            {option.label || option.value}
          </MenuItem>
        ))}
      </Select>
      {helperText && <FormHelperText>{helperText}</FormHelperText>}
    </FormControl>
  );
};

export default SelectField;
