import React, { useState } from "react";
import {
  TooltipProps,
  ClickAwayListener,
  Tooltip as MuiTooltip,
  makeStyles,
  createStyles
} from "@material-ui/core";

type Props = {
  title: string;
  children: React.ReactElement;
  trigger?: "click" | "hover" | "focus";
  placement?: TooltipProps["placement"];
};

const useStyles = makeStyles(() =>
  createStyles({
    clickButton: {
      border: 0,
      margin: 0,
      padding: 0,
      outline: "none",
      background: "unset"
    }
  })
);

const Tooltip: React.FC<Props> = ({ title, trigger, placement, children }) => {
  const classes = useStyles();
  const [clickOpen, setClickOpen] = useState(false);

  const handleTooltip = (state: boolean) => () => {
    setClickOpen(state);
  };

  if (trigger === "click") {
    return (
      <ClickAwayListener onClickAway={handleTooltip(false)}>
        <MuiTooltip
          title={title}
          open={clickOpen}
          disableFocusListener
          disableHoverListener
          disableTouchListener
          onClose={handleTooltip(false)}
          PopperProps={{ disablePortal: true }}
        >
          <span
            className={classes.clickButton}
            onClick={handleTooltip(!clickOpen)}
          >
            {children}
          </span>
        </MuiTooltip>
      </ClickAwayListener>
    );
  }

  return (
    <MuiTooltip
      title={title}
      placement={placement}
      disableFocusListener={trigger === "hover"}
      disableTouchListener={trigger === "hover"}
      disableHoverListener={trigger === "focus"}
    >
      {children}
    </MuiTooltip>
  );
};

export default Tooltip;
