import React from "react";
import {
  FormControl,
  FormLabel,
  FormGroup,
  Checkbox,
  CheckboxProps,
  FormHelperText,
  FormControlProps,
  FormControlLabel
} from "@material-ui/core";

type Props = CheckboxProps & {
  label?: string;
  value?: boolean;
  error?: boolean;
  helperText?: string;
  color?: FormControlProps["color"];
};

const CheckboxField: React.FC<Props> = ({
  color = "secondary",
  name,
  value,
  onChange,
  error,
  required,
  disabled,
  label,
  helperText,
  size
}) => {
  return (
    <FormControl
      error={error}
      color={color}
      required={required}
      disabled={disabled}
    >
      <FormLabel>{label}</FormLabel>
      <FormGroup>
        <FormControlLabel
          label=""
          control={
            <Checkbox
              size={size}
              name={name}
              color={color}
              checked={value}
              disabled={disabled}
              onChange={onChange}
            />
          }
        />
      </FormGroup>
      <FormHelperText>{helperText}</FormHelperText>
    </FormControl>
  );
};

export default CheckboxField;
