import React from "react";
import {
  Card as MuiCard,
  CardProps,
  CardHeader,
  CardMedia,
  CardContent,
  CardActions,
  CardActionArea
} from "@material-ui/core";

type CardType = React.FC<CardProps> & {
  Header?: any;
  Media?: any;
  Body?: any;
  Footer?: any;
  ActionArea?: any;
};

const Card: CardType = ({ children, ...props }) => {
  return <MuiCard {...props}>{children}</MuiCard>;
};

Card.Header = CardHeader;
Card.Media = CardMedia;
Card.Body = CardContent;
Card.Footer = CardActions;
Card.ActionArea = CardActionArea;

export default Card;
