import React, { Fragment, useState } from "react";
import {
  ListItem,
  ListItemIcon,
  ListItemText,
  Collapse,
  Theme,
  makeStyles,
  createStyles
} from "@material-ui/core";
import {
  ExpandLess as ExpandLessIcon,
  ExpandMore as ExpandMoreIcon
} from "@material-ui/icons";

type Props = {
  title: string;
  open?: boolean;
  hidden?: boolean;
  selected?: boolean;
  onClose?: Function;
  onClick?: Function;
  icon?: React.ElementType;
  children?: React.ReactNode;
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    menuItem: {
      paddingLeft: 23,
      marginBottom: 3,
      paddingRight: 23
    },
    menuItemButton: {
      fontSize: 18,
      borderRadius: 4,
      fontWeight: theme.typography.fontWeightBold,
      color: (props: { selected: boolean }) =>
        `${
          props.selected ? theme.palette.common.white : theme.palette.grey[800]
        } !important`,
      backgroundColor: (props: { selected: boolean }) =>
        `${
          props.selected ? theme.palette.primary.main : "transparent"
        } !important`
    },
    menuItemIcon: {
      color: "currentColor"
    },
    collapse: {
      "& $menuItem": {
        paddingLeft: 16,
        paddingRight: 0
      }
    }
  })
);

const MenuDrawerItem: React.FC<Props> = ({
  title,
  open,
  hidden,
  selected,
  onClick,
  icon: Icon,
  children,
  onClose
}) => {
  const classes = useStyles({ selected: !!selected });
  const [collapse, setCollapse] = useState(open);

  if (hidden) return null;

  const handleClick = () => {
    if (children) return setCollapse(!collapse);
    if (onClick) onClick();
    if (onClose) onClose();
    return null;
  };

  return (
    <div className={classes.menuItem}>
      <ListItem
        button
        selected={selected}
        onClick={handleClick}
        data-testid="list-item"
        className={classes.menuItemButton}
      >
        {Icon && (
          <ListItemIcon className={classes.menuItemIcon}>
            <Icon />
          </ListItemIcon>
        )}
        <ListItemText primary={title} />
        {children && (
          <Fragment>
            {collapse ? <ExpandLessIcon /> : <ExpandMoreIcon />}
          </Fragment>
        )}
      </ListItem>
      {children && (
        <Collapse className={classes.collapse} in={collapse} unmountOnExit>
          {children}
        </Collapse>
      )}
    </div>
  );
};

export default MenuDrawerItem;
