import React, { useState, Fragment } from "react";
import {
  Menu,
  MenuProps,
  MenuItem,
  Theme,
  makeStyles,
  createStyles
} from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    prefix: {
      display: "flex",
      marginRight: theme.spacing(1)
    },
    suffix: {
      display: "flex",
      marginLeft: "auto",
      paddingLeft: theme.spacing(1)
    }
  })
);

interface Props extends Omit<MenuProps, "open"> {
  items: any[];
  children: React.ReactNode;
}

const Dropdown: React.FC<Props> = ({ children, items, ...props }) => {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = (event: any) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleItemClick = (callback: any, item: any) => () => {
    handleClose();
    if (callback) callback(item);
  };

  const childrenComponent = React.cloneElement(children as any, {
    onClick: handleClick
  });

  return (
    <Fragment>
      {childrenComponent}
      <Menu
        {...props}
        keepMounted
        open={!!anchorEl}
        anchorEl={anchorEl}
        onClose={handleClose}
      >
        {items.map(({ onClick, ...item }, index) => (
          <MenuItem
            {...item}
            key={index}
            onClick={handleItemClick(onClick, item)}
          >
            {item.prefix && <div className={classes.prefix}>{item.prefix}</div>}
            {item.label}
            {item.suffix && <div className={classes.suffix}>{item.suffix}</div>}
          </MenuItem>
        ))}
      </Menu>
    </Fragment>
  );
};

export default Dropdown;
