import React from "react";
import { makeStyles } from "@material-ui/core";
import { Skeleton as MuiSkeleton } from "@material-ui/lab";

const useStyles = makeStyles({
  container: {
    width: "100%",
    display: "flex"
  },
  skeleton: {
    width: "100%"
  },
  skeletonRect: {
    transform: "unset",
    borderRadius: "unset",
    marginBottom: 16,
    "&:first-child": {
      marginBottom: (props: { custom: boolean }) => (props.custom ? 16 : 32)
    }
  },
  skeletonAvatar: {
    flexShrink: 0,
    marginRight: 16
  }
});

type Props = {
  avatar?: boolean;
  active?: boolean;
  paragraphs?: number | number[] | string[];
};

const Skeleton: React.FC<Props> = ({ active, avatar, paragraphs }) => {
  const classes = useStyles({
    custom: typeof paragraphs === "number" || paragraphs
  });

  const getParagraphs = (): any[] => {
    if (typeof paragraphs === "number") {
      return Array.from(Array(paragraphs), () => "100%");
    }
    if (!paragraphs || !paragraphs.length) {
      return ["38%", "100%", "100%", "61%"];
    }
    return paragraphs;
  };

  return (
    <div className={classes.container}>
      {avatar && (
        <MuiSkeleton
          width={40}
          height={40}
          variant="circle"
          className={classes.skeletonAvatar}
          animation={active ? "wave" : false}
        />
      )}
      <div className={classes.skeleton}>
        {getParagraphs().map((paragraph, index: number) => (
          <MuiSkeleton
            key={index}
            height={16}
            width={paragraph}
            className={classes.skeletonRect}
            animation={active ? "wave" : false}
          />
        ))}
      </div>
    </div>
  );
};

export default Skeleton;
