import React from "react";
import { docsEnv } from "../../utils/environment";
import { Typography, Theme, makeStyles, createStyles } from "@material-ui/core";

type Props = {
  description?: string;
  extra?: React.ReactElement;
  actions?: React.ReactElement;
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    wrapper: {
      left: 0,
      bottom: 0,
      width: "100%",
      display: "flex",
      flexDirection: "column",
      padding: theme.spacing(3),
      justifyContent: "space-between",
      backgroundColor: theme.palette.grey[100],
      position: docsEnv() ? "relative" : "fixed",
      [theme.breakpoints.up("sm")]: {
        flexDirection: "row"
      }
    },
    column: {
      display: "flex",
      alignItems: "center"
    },
    actions: {
      fontSize: 14,
      marginLeft: theme.spacing(3),
      color: theme.palette.grey[600],
      "& a": {
        color: "inherit",
        textDecoration: "none",
        "&:hover": {
          color: theme.palette.grey[800]
        }
      }
    },
    extra: {
      display: "flex",
      alignItems: "center",
      color: theme.palette.grey[600],
      "& *": {
        marginLeft: 10
      }
    }
  })
);

const Footer: React.FC<Props> = ({ description, actions, extra }) => {
  const classes = useStyles();

  return (
    <div className={classes.wrapper}>
      <div className={classes.column}>
        <Typography variant="body2">{description}</Typography>
        <div className={classes.actions}>{actions}</div>
      </div>
      <div className={classes.column}>
        <div className={classes.extra}>{extra}</div>
      </div>
    </div>
  );
};

export default Footer;
