import React, { Fragment, useState, createContext, useContext } from "react";
import { IconButton, Snackbar } from "@material-ui/core";
import { Close as CloseIcon } from "@material-ui/icons";
import { Alert, AlertProps } from "@material-ui/lab";

const SnackbarContext = createContext({});

type Props = {
  children: React.ReactChild;
};

type Options = {
  duration?: number;
  closable?: boolean;
  autoHide?: boolean;
  vertical?: "bottom" | "top";
  horizontal?: "center" | "left" | "right";
};

type MessageType = {
  success?: any;
  warning?: any;
  error?: any;
  info?: any;
};

const SnackbarProvider: React.FC<Props> = ({ children }) => {
  const { Provider } = SnackbarContext;
  const [text, setText] = useState<string>("");
  const [type, setType] = useState<AlertProps["severity"]>();
  const [options, setOptions] = useState<Options>({});

  const handleClose = () => {
    setText("");
    setOptions({});
    if (type) setType(undefined);
  };

  const message = (value: string, config: Options) => {
    setTimeout(
      () => {
        setText(value);
        setOptions(config || {});
      },
      text ? 300 : 0
    );
    setText(value);
    setOptions(config || {});
  };

  ["success", "warning", "error", "info"].forEach((fnType) => {
    message[fnType] = (value: string, config: Options) => {
      setTimeout(
        () => {
          setText(value);
          setOptions(config || {});
          setType(fnType as AlertProps["severity"]);
        },
        text ? 300 : 0
      );
    };
  });

  return (
    <Fragment>
      {!!text && (
        <Snackbar
          open={!!text}
          message={text}
          onClose={handleClose}
          autoHideDuration={
            options.autoHide !== false ? options.duration || 6000 : undefined
          }
          anchorOrigin={{
            vertical: options.vertical || "bottom",
            horizontal: options.horizontal || "center"
          }}
          action={
            options.closable == null || options.closable ? (
              <IconButton size="small" color="inherit" onClick={handleClose}>
                <CloseIcon fontSize="small" />
              </IconButton>
            ) : undefined
          }
        >
          {type && (
            <Alert severity={type} onClose={handleClose}>
              {text}
            </Alert>
          )}
        </Snackbar>
      )}
      <Provider value={{ message }}>{children}</Provider>
    </Fragment>
  );
};

const useSnackbar = (): { message: MessageType } =>
  useContext(SnackbarContext) as any;

export default SnackbarProvider;
export { useSnackbar };
