import React from "react";
import { docsEnv } from "../../utils/environment";
import { Close as CloseIcon } from "@material-ui/icons";
import {
  Theme,
  makeStyles,
  DrawerProps,
  createStyles,
  Drawer as MuiDrawer
} from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      fontSize: theme.spacing(2),
      padding: `${theme.spacing(2)}px ${theme.spacing(3)}px`
    },
    header: {
      display: "flex",
      alignItems: "center",
      fontSize: theme.spacing(2),
      justifyContent: "space-between",
      borderBottom: `1px solid ${theme.palette.grey[300]}`,
      padding: `${theme.spacing(2)}px ${theme.spacing(3)}px`
    },
    headerIcon: {
      cursor: "pointer",
      marginLeft: theme.spacing(3)
    }
  })
);

const Drawer: React.FC<DrawerProps> = ({
  title,
  open,
  children,
  onClose,
  ...props
}) => {
  const classes = useStyles();
  const position = docsEnv() ? "absolute" : "fixed";

  return (
    <MuiDrawer
      {...props}
      open={open}
      onClose={onClose}
      style={{ position }}
      disablePortal={docsEnv()}
      PaperProps={{ style: { position } }}
      BackdropProps={{ style: { position } }}
    >
      <div>
        {title && (
          <div className={classes.header}>
            <span>{title}</span>
            <CloseIcon
              fontSize="small"
              onClick={onClose as any}
              className={classes.headerIcon}
            />
          </div>
        )}
        {children && <div className={classes.container}>{children}</div>}
      </div>
    </MuiDrawer>
  );
};

export default Drawer;
