import React from "react";
import { TabType } from "./types";
import {
  Tabs as MuiTabs,
  TabsProps,
  Tab as MuiTab,
  makeStyles,
  createStyles
} from "@material-ui/core";

interface Props extends Omit<TabsProps, "onChange"> {
  onChange?: any;
  items: TabType[];
  scrollable?: boolean;
  indicatorColor?: "secondary" | "primary";
  textColor?: "secondary" | "primary" | "inherit";
  children?: (args: { tab: number; setTab: any }) => React.ReactNode;
}

const useStyles = makeStyles(() =>
  createStyles({
    tab: {
      "&:disabled": {
        cursor: "not-allowed",
        pointerEvents: "unset"
      }
    }
  })
);

const Tabs: React.FC<Props> = ({
  items,
  value,
  scrollable,
  children,
  textColor,
  onChange,
  indicatorColor,
  ...props
}) => {
  const classes = useStyles();

  return (
    <MuiTabs
      value={value}
      onChange={onChange}
      textColor={textColor}
      indicatorColor={indicatorColor}
      scrollButtons={scrollable ? "on" : "off"}
      variant={scrollable ? "scrollable" : "standard"}
      {...props}
    >
      {items.map(({ icon: Icon, ...item }, index) => (
        <MuiTab
          key={index}
          label={item.label}
          className={classes.tab}
          disabled={item.disabled}
          icon={Icon ? <Icon /> : undefined}
        />
      ))}
    </MuiTabs>
  );
};

export default Tabs;
