import React from "react";
import {
  Radio as MuiRadio,
  RadioProps,
  FormGroup,
  FormControlLabel
} from "@material-ui/core";

type Props = {
  label?: String;
  value?: boolean;
  disabled?: boolean;
  size?: RadioProps["size"];
  color?: RadioProps["color"];
  onChange?: RadioProps["onChange"];
};

const Radio: React.FC<Props> = ({
  size,
  value,
  color,
  label,
  onChange,
  disabled
}) => {
  const commonProps = {
    size,
    color,
    value,
    disabled,
    onChange: onChange || undefined
  };

  return label ? (
    <FormGroup style={{ display: "inline-block" }} row>
      <FormControlLabel label={label} control={<MuiRadio {...commonProps} />} />
    </FormGroup>
  ) : (
    <MuiRadio {...commonProps} />
  );
};

export default Radio;
