import React, { useEffect } from "react";
import MenuDrawerItem from "./MenuDrawerItem";
import composeComponent from "../../utils/composeComponent";
import { docsEnv } from "../../utils/environment";
import {
  Drawer,
  List,
  Theme,
  makeStyles,
  createStyles
} from "@material-ui/core";

type Props = {
  open: boolean;
  children: any;
  setOpen: Function;
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    toolbar: theme.mixins.toolbar,
    drawer: {
      [theme.breakpoints.up("sm")]: {
        flexShrink: 0
      }
    },
    drawerRoot: {
      zIndex: "1000 !important" as any
    },
    drawerPaper: {
      width: 345,
      zIndex: 1000,
      boxShadow: theme.shadows[10],
      paddingTop: theme.spacing(8),
      backgroundColor: theme.palette.grey[100]
    },
    menuList: {
      [theme.breakpoints.up("sm")]: {
        marginTop: 20
      }
    },
    whiteIcon: {
      color: theme.palette.common.white
    },
    primaryIcon: {
      color: theme.palette.primary.main
    }
  })
);

const MenuDrawer: React.FC<Props> = ({ open, setOpen, children }) => {
  const classes = useStyles();
  const position = docsEnv() ? "absolute" : "fixed";

  const onClose = () => {
    return setOpen(false);
  };

  useEffect(() => {
    if (open && docsEnv()) document.body.style.overflow = "auto";
  }, [open]);

  return (
    <nav className={classes.drawer}>
      <Drawer
        open={open}
        onClose={onClose()}
        style={{ position }}
        disablePortal={docsEnv()}
        PaperProps={{ style: { position } }}
        BackdropProps={{ style: { position } }}
        classes={{ paper: classes.drawerPaper, root: classes.drawerRoot }}
      >
        <List className={classes.menuList}>
          {children({ Item: composeComponent({ onClose }, MenuDrawerItem) })}
        </List>
      </Drawer>
    </nav>
  );
};

export default MenuDrawer;
