import React from "react";
import Floating from "./Floating";
import {
  Button as MuiButton,
  ButtonProps,
  IconButton,
  ButtonTypeMap
} from "@material-ui/core";

type Props = ButtonProps & {
  component?: any;
  prefix?: React.ReactNode;
  suffix?: React.ReactNode;
  icon?: React.ElementType;
  children?: React.ReactNode;
  color?: ButtonProps["color"];
  variant?: ButtonProps["variant"];
  size?: ButtonTypeMap["props"]["size"];
  href?: ButtonTypeMap["props"]["href"];
};

const Button: React.FC<Props> = ({
  children,
  variant,
  prefix,
  suffix,
  size,
  icon: IconComponent,
  ...props
}) => {
  const commonProps = {
    variant: variant || "contained",
    ...props
  };

  if (IconComponent) {
    return (
      <IconButton {...commonProps} size={size === "small" ? "small" : "medium"}>
        <IconComponent />
      </IconButton>
    );
  }

  return (
    <MuiButton
      size={size}
      endIcon={suffix}
      startIcon={prefix}
      style={{ minWidth: "auto" }}
      {...commonProps}
    >
      {children}
    </MuiButton>
  );
};

(Button as any).Floating = Floating;

export default Button;
