import React, { useState, useEffect } from "react";
import cn from "classnames";
import { useDropzone } from "react-dropzone";
import { IconButton, Theme, makeStyles, createStyles } from "@material-ui/core";
import {
  Add as AddIcon,
  Delete as DeleteIcon,
  FileCopy as FileIcon
} from "@material-ui/icons";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    wrapper: {
      display: "flex"
    },
    visibleDropzone: {
      display: "block"
    },
    dropzone: {
      width: 104,
      height: 104,
      fontSize: 14,
      marginRight: 8,
      marginBottom: 8,
      borderRadius: 4,
      display: "flex",
      outline: "none",
      cursor: "pointer",
      textAlign: "center",
      alignItems: "center",
      boxSizing: "border-box",
      flexDirection: "column",
      justifyContent: "center",
      backgroundColor: theme.palette.grey[100],
      border: `1px dashed ${theme.palette.grey[300]}`,
      transition: `border-color ${theme.transitions.duration.short}ms ${theme.transitions.easing.easeInOut}`,
      "&:hover": {
        borderColor: theme.palette.primary.main
      }
    },
    file: {
      padding: 8,
      width: 104,
      height: 104,
      marginRight: 8,
      marginBottom: 8,
      borderRadius: 4,
      outline: "none",
      display: "flex",
      position: "relative",
      alignItems: "center",
      boxSizing: "border-box",
      justifyContent: "center",
      border: `1px dashed ${theme.palette.grey[300]}`,
      "&:hover $fileOverlay": {
        display: "flex"
      }
    },
    fileImage: {
      width: "100%",
      height: "100%",
      borderRadius: 3,
      objectFit: "cover"
    },
    fileOverlay: {
      zIndex: 1,
      width: 86,
      height: 86,
      borderRadius: 3,
      display: "none",
      position: "absolute",
      alignItems: "center",
      justifyContent: "center",
      backgroundColor: "rgba(0, 0, 0, .8)"
    },
    fileDelete: {
      color: theme.palette.common.white
    }
  })
);

type Props = {
  name: string;
  onChange: any;
  label?: string;
  accept?: string;
  maxFiles?: number;
  multiple?: boolean;
  disabled?: boolean;
  icon?: React.ElementType;
};

const UploadWall: React.FC<Props> = ({
  name,
  label,
  multiple,
  maxFiles,
  accept,
  onChange,
  icon: Icon
}) => {
  const classes = useStyles();
  const IconComponent = Icon || AddIcon;
  const [previews, setPreviews] = useState<any>([]);
  const [acceptedFiles, setAcceptedFiles] = useState<any>([]);
  const { getRootProps, getInputProps } = useDropzone({
    maxFiles,
    multiple,
    accept,
    onDrop: (files) => {
      if (onChange) onChange(files);
      setAcceptedFiles(files);
    }
  });

  useEffect(() => {
    const promises = acceptedFiles.map(
      (file: any) =>
        new Promise((resolve: any) => {
          const image = new Image();
          const url = URL.createObjectURL(file);
          image.src = url;
          image.onerror = () => resolve(undefined);
          image.onload = () => resolve(url);
        })
    );

    Promise.all(promises).then(setPreviews);
  }, [acceptedFiles]);

  const handleDelete = (index: number) => () => {
    setPreviews(
      previews.filter((_: any, previewIndex: number) => previewIndex !== index)
    );
    setAcceptedFiles(
      acceptedFiles.filter((_: any, fileIndex: number) => fileIndex !== index)
    );
  };

  return (
    <div className={classes.wrapper}>
      {acceptedFiles.map((file: any, index: number) => (
        <div className={classes.file} key={index}>
          <div className={classes.fileOverlay}>
            <IconButton onClick={handleDelete(index)}>
              <DeleteIcon className={classes.fileDelete} />
            </IconButton>
          </div>
          {previews[index] ? (
            <img
              alt={file.name}
              src={previews[index]}
              className={classes.fileImage}
            />
          ) : (
            <FileIcon />
          )}
        </div>
      ))}
      <div
        {...getRootProps({
          className: cn(
            classes.dropzone,
            (multiple || (!multiple && !acceptedFiles.length)) &&
              classes.visibleDropzone
          )
        })}
      >
        <input name={name} {...getInputProps()} />
        <IconComponent />
        <span>{label || "Upload"}</span>
      </div>
    </div>
  );
};

export default UploadWall;
