import React, { useState } from "react";
import cn from "classnames";
import UploadWall from "./Wall";
import UploadButton from "./Button";
import Draggable from "./Draggable";
import { v4 as uuid } from "uuid";
import {
  List,
  ListItem,
  Tooltip,
  CircularProgress,
  ListItemIcon,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  LinearProgress,
  ButtonProps,
  Theme,
  createStyles,
  makeStyles
} from "@material-ui/core";
import {
  Close as CloseIcon,
  Attachment as AttachmentIcon
} from "@material-ui/icons";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    list: {
      paddingLeft: 5,
      color: theme.palette.grey[600]
    },
    listError: {
      borderWidth: 1,
      borderStyle: "solid",
      color: theme.palette.error.main,
      borderColor: theme.palette.error.light
    },
    listContainer: {
      "&:hover $listAction": {
        display: "block"
      }
    },
    listIcon: {
      minWidth: 30,
      color: "inherit"
    },
    progressBar: {
      width: "15%",
      borderRadius: 5,
      marginRight: "auto"
    },
    label: {
      flex: "unset",
      marginRight: 10
    },
    listAction: {
      display: "none"
    },
    input: {
      display: "none"
    },
    errorTooltip: {
      display: "none"
    }
  })
);

type Props = {
  name: string;
  label?: string;
  wall?: boolean;
  accept?: string;
  maxFiles?: number;
  multiple?: boolean;
  disabled?: boolean;
  draggable?: boolean;
  onChange?: Function;
  icon?: React.ElementType;
  color?: ButtonProps["color"];
};

type FileType = {
  id?: string;
  result?: any;
  name?: string;
  error?: boolean;
  loading?: boolean;
  progress?: number;
};

const Upload: React.FC<Props> = ({
  name,
  wall,
  label,
  accept,
  onChange,
  multiple,
  maxFiles,
  disabled,
  draggable,
  color,
  icon: Icon
}) => {
  const classes = useStyles();
  const [files, setFiles] = useState<FileType[]>([]);

  const handleFileRemove = (index: number) => () => {
    setFiles(files.filter((_, fileIndex) => fileIndex !== index));
  };

  const handleFileSave = (id: string) => (file: FileType) => {
    if (!multiple) {
      setFiles((currentFiles) => {
        const hasFile = currentFiles.find(
          (currentFile) => currentFile.id === id
        );
        return hasFile ? [{ ...hasFile, ...file }] : [{ id, ...file }];
      });
    }

    setFiles((currentFiles) => {
      const hasFile = currentFiles.some((currentFile) => currentFile.id === id);
      if (!hasFile) return [...currentFiles, { id, ...file }];
      return currentFiles.map((currentFile) =>
        currentFile.id === id ? { ...currentFile, ...file } : currentFile
      );
    });
  };

  const uploadImage = (file: any) => {
    if (file) {
      const reader = new FileReader();
      const saveFile = handleFileSave(uuid());

      reader.readAsDataURL(file);

      reader.addEventListener("loadstart", () => {
        saveFile({ name: file.name, loading: true, progress: 0 });
      });

      reader.addEventListener("loadend", () => {
        saveFile({ loading: false });
      });

      reader.addEventListener("error", () => {
        saveFile({ loading: false, result: null, error: true });
      });

      reader.addEventListener("progress", (event) => {
        const progress = (100 * event.loaded) / event.total;
        saveFile({ progress });
      });

      reader.addEventListener("load", () => {
        saveFile({ loading: false, error: false, result: file });
      });
    }
  };

  const handleDraggable = (acceptedFiles: any) => {
    setFiles(acceptedFiles);
    if (onChange) onChange(acceptedFiles);
  };

  const handleImage = (input: any) => {
    if (!multiple) {
      if (onChange) onChange(input);
      return uploadImage(input.target.files[0]);
    }

    return Array.from(input.target.files).forEach((_, index: number) => {
      const file = input.target.files[index];
      uploadImage(file);
      if (onChange) onChange(input);
    });
  };

  if (wall) {
    return (
      <UploadWall
        icon={Icon}
        name={name}
        label={label}
        disabled={disabled}
        maxFiles={maxFiles}
        multiple={multiple}
        onChange={onChange}
      />
    );
  }

  return (
    <div>
      {draggable ? (
        <Draggable
          name={name}
          icon={Icon}
          label={label}
          accept={accept}
          multiple={multiple}
          onChange={handleDraggable}
        />
      ) : (
        <UploadButton
          name={name}
          icon={Icon}
          label={label}
          color={color}
          accept={accept}
          multiple={multiple}
          onChange={handleImage}
        />
      )}
      <List dense>
        {files.map((file: any, index) => (
          <Tooltip
            key={index}
            placement="top"
            title="Erro ao realizar upload"
            classes={{ popper: cn(!file.error && classes.errorTooltip) }}
          >
            <ListItem
              button
              classes={{
                container: classes.listContainer,
                root: cn(classes.list, file.error && classes.listError)
              }}
            >
              <ListItemIcon className={classes.listIcon}>
                {file.loading ? (
                  <CircularProgress size={16} />
                ) : (
                  <AttachmentIcon />
                )}
              </ListItemIcon>
              <ListItemText className={classes.label} primary={file.name} />
              {file.loading && (
                <LinearProgress
                  variant="determinate"
                  value={file.progress}
                  className={classes.progressBar}
                />
              )}
              <ListItemSecondaryAction>
                <IconButton
                  edge="end"
                  size="small"
                  className={classes.listAction}
                  onClick={handleFileRemove(index)}
                >
                  <CloseIcon />
                </IconButton>
              </ListItemSecondaryAction>
            </ListItem>
          </Tooltip>
        ))}
      </List>
    </div>
  );
};

export default Upload;
