import React, { useEffect, useRef } from "react";
import { useDropzone } from "react-dropzone";
import { Theme, makeStyles, createStyles } from "@material-ui/core";
import { Add as AddIcon } from "@material-ui/icons";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    wrapper: {
      padding: 16,
      width: "100%",
      height: "100%",
      display: "flex",
      outline: "none",
      cursor: "pointer",
      alignItems: "center",
      boxSizing: "border-box",
      flexDirection: "column",
      justifyContent: "center",
      backgroundColor: theme.palette.grey[100],
      border: `1px dashed ${theme.palette.grey[300]}`,
      transition: `border-color ${theme.transitions.duration.short}ms ${theme.transitions.easing.easeInOut}`,
      "&:hover": {
        borderColor: theme.palette.primary.main
      }
    }
  })
);

type Props = {
  name: string;
  onChange: any;
  label?: string;
  accept?: string;
  maxFiles?: number;
  multiple?: boolean;
  icon?: React.ElementType;
};

const UploadDragabble: React.FC<Props> = ({
  name,
  label,
  accept,
  onChange,
  multiple,
  maxFiles,
  icon: Icon
}) => {
  const classes = useStyles();
  const didMountRef = useRef(false);
  const IconComponent = Icon || AddIcon;
  const { acceptedFiles, getRootProps, getInputProps } = useDropzone({
    accept,
    multiple,
    maxFiles
  });

  useEffect(() => {
    if (didMountRef.current && onChange) onChange(acceptedFiles);
    didMountRef.current = true;
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [acceptedFiles]);

  return (
    <div {...getRootProps({ className: classes.wrapper })}>
      <input name={name} {...getInputProps()} />
      <IconComponent />
      <span>{label || "Upload"}</span>
    </div>
  );
};

export default UploadDragabble;
