import React from "react";
import {
  Switch as MuiSwitch,
  SwitchProps,
  FormGroup,
  FormControlLabel
} from "@material-ui/core";

type Props = {
  label?: String;
  value?: boolean;
  disabled?: boolean;
  size?: SwitchProps["size"];
  color?: SwitchProps["color"];
  onChange?: SwitchProps["onChange"];
};

const Switch: React.FC<Props> = ({
  size,
  value,
  color,
  label,
  onChange,
  disabled
}) => {
  const commonProps = {
    size,
    color,
    disabled,
    checked: value,
    onChange: onChange || undefined
  };

  return label ? (
    <FormGroup style={{ display: "inline-block" }} row>
      <FormControlLabel
        label={label}
        control={<MuiSwitch {...commonProps} />}
      />
    </FormGroup>
  ) : (
    <MuiSwitch {...commonProps} />
  );
};

export default Switch;
