import React from "react";
import {
  Switch,
  FormControl,
  FormLabel,
  FormHelperText,
  SwitchProps,
  FormControlProps
} from "@material-ui/core";

type Props = SwitchProps & {
  name?: string;
  label?: string;
  value?: boolean;
  error?: boolean;
  required?: string;
  helperText?: string;
  size?: SwitchProps["size"];
  color?: FormControlProps["color"];
  onChange?: SwitchProps["onChange"];
};

const SwitchField: React.FC<Props> = ({
  color = "secondary",
  value,
  onChange,
  label,
  helperText,
  error,
  disabled,
  required,
  size,
  name
}) => {
  return (
    <FormControl
      name={name}
      size={size}
      error={error}
      color={color}
      disabled={disabled}
      required={required}
      component="fieldset"
    >
      <FormLabel component="legend">{label}</FormLabel>
      <Switch
        size={size}
        name={name}
        color={color}
        checked={value}
        onChange={onChange}
      />
      <FormHelperText>{helperText}</FormHelperText>
    </FormControl>
  );
};

export default SwitchField;
