import React from "react";
import DialogTitle from "./Title";
import {
  Dialog as MuiDialog,
  DialogProps,
  DialogContent,
  DialogActions,
  Button
} from "@material-ui/core";

type Props = {
  onSave?: any;
  onClose?: any;
  title?: string;
  saveText?: string;
  cancelText?: string;
  hideFooter: boolean;
  open: DialogProps["open"];
  width?: DialogProps["maxWidth"];
  children?: React.ReactNode | any;
};

const Dialog: React.FC<Props> = ({
  children,
  open,
  cancelText,
  onClose,
  onSave,
  saveText,
  title,
  hideFooter,
  width
}) => {
  return (
    <MuiDialog
      open={open}
      maxWidth={width}
      onClose={onClose}
      fullWidth={!!width}
    >
      {title && <DialogTitle onClose={onClose}>{title}</DialogTitle>}
      <DialogContent dividers>{children}</DialogContent>
      {!hideFooter && (
        <DialogActions>
          <Button onClick={onClose}>{cancelText || "Cancelar"}</Button>
          <Button
            color="primary"
            disableElevation
            onClick={onSave}
            variant="contained"
          >
            {saveText || "Salvar"}
          </Button>
        </DialogActions>
      )}
    </MuiDialog>
  );
};

export default Dialog;
