import React from "react";
import {
  Typography,
  Breadcrumbs as MuiBreadcrumbs,
  BreadcrumbsProps,
  Theme,
  makeStyles,
  createStyles
} from "@material-ui/core";

type BreadcrumbItem = {
  as?: any;
  title?: string;
  props?: object;
  icon?: React.ElementType;
};

type Props = BreadcrumbsProps & {
  separator?: any;
  items: BreadcrumbItem[];
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    wrapper: {
      display: "flex"
    },
    icon: {
      marginRight: theme.spacing(0.5),
      width: 20,
      height: 20
    }
  })
);

const Breadcrumbs: React.FC<Props> = ({ items, separator, ...props }) => {
  const classes = useStyles();

  const isLastChild = (index: number) => {
    return index === items.length - 1;
  };

  return (
    <MuiBreadcrumbs separator={separator} {...props}>
      {items.map(({ icon: Icon, ...item }, index) => (
        <Typography
          key={index}
          className={classes.wrapper}
          component={item.as || "div"}
          color={isLastChild(index) ? "textPrimary" : "inherit"}
          {...item.props}
        >
          {Icon && <Icon className={classes.icon} />}
          {item.title}
        </Typography>
      ))}
    </MuiBreadcrumbs>
  );
};

export default Breadcrumbs;
