import React from "react";
import {
  FormControl,
  FormControlProps,
  FormLabel,
  RadioGroup,
  RadioGroupProps,
  FormControlLabel,
  Radio,
  RadioProps,
  FormHelperText
} from "@material-ui/core";

type InputProps = RadioProps & {
  label?: string | number;
};

type Props = RadioGroupProps & {
  label?: string;
  required?: boolean;
  helperText?: string;
  items: InputProps[];
  size?: FormControlProps["size"];
  color?: FormControlProps["color"];
};

const RadioField: React.FC<Props> = ({
  color = "secondary",
  items,
  value,
  name,
  onChange,
  label,
  size,
  helperText,
  required
}) => {
  return (
    <FormControl required={required} color={color}>
      <FormLabel style={{ marginBottom: 8 }}>{label}</FormLabel>
      <RadioGroup
        row
        name={name}
        color={color}
        value={value}
        onChange={onChange}
      >
        {items.map((item: any, index: number) => (
          <FormControlLabel
            {...item}
            key={index}
            control={<Radio size={size} color={color} />}
          />
        ))}
      </RadioGroup>
      <FormHelperText>{helperText}</FormHelperText>
    </FormControl>
  );
};

export default RadioField;
