import React, { Fragment } from "react";
import cn from "classnames";
import { docsEnv } from "../../utils/environment";
import { Theme, makeStyles, createStyles, Backdrop } from "@material-ui/core";

type Props = {
  showBackdrop?: boolean;
  children: React.ReactNode;
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    wrapper: {
      zIndex: 1000,
      position: docsEnv() ? "absolute" : "fixed",
      backgroundColor: theme.palette.common.white
    }
  })
);

const Wrapper: React.FC<Props> = ({ showBackdrop, children }) => {
  const classes = useStyles();

  return showBackdrop ? (
    <Backdrop className={cn(classes.wrapper)} open>
      {children}
    </Backdrop>
  ) : (
    <Fragment>{children}</Fragment>
  );
};

export default Wrapper;
