import React from "react";
import cn from "classnames";
import { docsEnv } from "../../utils/environment";
import {
  Fab,
  FabProps,
  Theme,
  makeStyles,
  createStyles
} from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    button: (props: {
      fixed: boolean;
      vertical: TypeFixed["vertical"];
      horizontal: TypeFixed["horizontal"];
    }) => ({
      [props.vertical || "bottom"]: theme.spacing(4),
      [props.horizontal || "right"]: theme.spacing(4)
    })
  })
);

type TypeFixed = {
  vertical?: "top" | "bottom";
  horizontal?: "left" | "right";
};

type Props = {
  fixed?: TypeFixed;
  children: FabProps["children"];
  className?: FabProps["className"];
};

const Floating: React.FC<Props> = ({
  children,
  className,
  fixed,
  ...props
}) => {
  const classes = useStyles({
    fixed: !!fixed,
    vertical: fixed?.vertical,
    horizontal: fixed?.horizontal
  });

  if (fixed) {
    return (
      <Fab
        {...props}
        data-testid="test-fab"
        className={cn(classes.button, className)}
        style={{ position: docsEnv() ? "absolute" : "fixed" }}
      >
        {children}
      </Fab>
    );
  }

  return (
    <Fab {...props} data-testid="test-fab" className={className}>
      {children}
    </Fab>
  );
};

export default Floating;
