import React, { Fragment } from "react";
import cn from "classnames";
import {
  Paper,
  Typography,
  Theme,
  makeStyles,
  createStyles
} from "@material-ui/core";
import {
  Timeline as MuiTimeline,
  TimelineItem,
  TimelineSeparator,
  TimelineDot,
  TimelineConnector,
  TimelineContent,
  TimelineDotProps
} from "@material-ui/lab";

type ItemProps = {
  title: String;
  outlined?: boolean;
  description?: String;
  icon?: React.ElementType;
  color: TimelineDotProps["color"];
};

type Props = {
  cards?: boolean;
  items: ItemProps[];
  alternate?: boolean;
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paper: {
      padding: "6px 16px"
    },
    dotCards: {
      width: 24,
      height: 24
    },
    icon: {
      fontSize: 22,
      margin: "8px -5px",
      color: theme.palette.grey[400]
    },
    colorInherit: {
      color: "currentColor"
    },
    backgroundInherit: {
      backgroundColor: "currentColor"
    },
    borderInherit: {
      borderColor: "currentColor"
    },
    colorPrimary: {
      color: theme.palette.primary.main
    },
    backgroundPrimary: {
      backgroundColor: theme.palette.primary.main
    },
    borderPrimary: {
      borderColor: theme.palette.primary.main
    },
    colorSecondary: {
      color: theme.palette.secondary.main
    },
    backgroundSecondary: {
      backgroundColor: theme.palette.secondary.main
    },
    borderSecondary: {
      borderColor: theme.palette.secondary.main
    }
  })
);

const Timeline: React.FC<Props> = ({ items, alternate, cards }) => {
  const classes = useStyles();

  const isLastItem = (index: number) => {
    return items.length - 1 === index;
  };

  const getColorClassnames = (color: TimelineDotProps["color"]) => {
    return cn(
      classes.icon,
      color === "inherit" && classes.colorInherit,
      color === "primary" && classes.colorPrimary,
      color === "secondary" && classes.colorSecondary
    );
  };

  const getBackgroundClassnames = (
    color: TimelineDotProps["color"],
    hasCards: boolean = false
  ) => {
    return cn(
      hasCards && classes.dotCards,
      color === "inherit" && classes.backgroundInherit,
      color === "primary" && classes.backgroundPrimary,
      color === "secondary" && classes.backgroundSecondary
    );
  };

  const getBorderClassnames = (
    color: TimelineDotProps["color"],
    hasCards: boolean = false
  ) => {
    return cn(
      hasCards && classes.dotCards,
      color === "inherit" && classes.borderInherit,
      color === "primary" && classes.borderPrimary,
      color === "secondary" && classes.borderSecondary
    );
  };

  return (
    <MuiTimeline align={alternate ? "alternate" : "left"}>
      {items.map(({ icon: Icon, ...item }, index) => (
        <TimelineItem key={index}>
          <TimelineSeparator>
            {cards ? (
              <TimelineDot
                variant={item.outlined ? "outlined" : "default"}
                className={
                  item.outlined
                    ? getBorderClassnames(item.color, true)
                    : getBackgroundClassnames(item.color, true)
                }
              >
                {Icon && <Icon />}
              </TimelineDot>
            ) : (
              <Fragment>
                {Icon ? (
                  <Icon className={getColorClassnames(item.color)} />
                ) : (
                  <TimelineDot
                    className={
                      item.outlined
                        ? getBorderClassnames(item.color)
                        : getBackgroundClassnames(item.color)
                    }
                    variant={item.outlined ? "outlined" : "default"}
                  />
                )}
              </Fragment>
            )}
            {!isLastItem(index) && <TimelineConnector />}
          </TimelineSeparator>
          <TimelineContent>
            {cards ? (
              <Paper elevation={3} className={classes.paper}>
                <Typography variant="h6" component="h1">
                  {item.title}
                </Typography>
                {item.description && (
                  <Typography>{item.description}</Typography>
                )}
              </Paper>
            ) : (
              <Typography>{item.title}</Typography>
            )}
          </TimelineContent>
        </TimelineItem>
      ))}
    </MuiTimeline>
  );
};

export default Timeline;
